// import {hcaptcha} from "@shubhamranjan/vue-hcaptcha";
import imgLogo from '@/assets/logo.png';
import logoImgLight from "@/assets/newHome/logo/Logo_Transparent.png"; //WEIXIN_SEND_SMS

import { EMAIL_REGISTER_V2, SEND_EMAIL_CODE, WEIXIN_SEND_SMS, ZOHO_SYNC, PHONE_REGISTER_V2 } from "@/api/api"; //LINKEDIN_CODE

import { useRoute, useRouter } from "vue-router";
import axios from "axios";
import { randomString } from '@/utils';
import { useStore } from 'vuex';
import { reactive, ref } from "vue";
import { decode } from "js-base64";
export default {
  name: "index",

  data() {
    return {
      logoImgLight,
      completeBtnDisabledStatus: true,
      companyName: '',
      createAccountDisabledStatus: true,
      stepValue: 1,
      imgLogo,
      identityBusinessCheckedStatus: false,
      submitRegisterLoadingStatus: false,
      humanVerifyStatus: true,
      remeberValue: false,
      identityValue: 0,
      sendCodeLoading: false,
      showValue: 'login',
      businessDialogStatus: false
    };
  },

  components: {},

  setup() {
    let router = useRouter();
    let route = useRoute();

    const getParams = () => {
      console.log(route.query);
      return route.query;
    };

    const forgotDialogVisible = ref(false);

    const skipHomePage = () => {
      let a = route.query.redirect_params;

      if (a) {
        let b = JSON.parse(decode(a));

        if (b.path) {
          return router.push({
            path: b.path,
            query: b.query
          });
        }
      }

      return router.push({
        path: '/'
      });
    };

    let value = route.query.type;
    const showType = value ? value : 'login';
    const store = useStore();

    const setCurrentUser = data => store.commit('currentUser', data);

    let checkCodeBtn = reactive({
      text: 'SEND CODE',
      loading: false,
      disabled: false,
      duration: 10,
      timer: null
    });

    const getCheckCodeTimer = () => {
      if (checkCodeBtn.duration !== 10) {
        checkCodeBtn.disabled = true;
        checkCodeBtn.loading = true;
      } // 清除定时器


      checkCodeBtn.timer && clearInterval(checkCodeBtn.timer); //开启定时器

      checkCodeBtn.timer = setInterval(() => {
        const tmp = checkCodeBtn.duration--;
        checkCodeBtn.text = `${tmp} S`;
        checkCodeBtn.loading = true;
        checkCodeBtn.disabled = true;

        if (tmp <= 0) {
          //清除定时器
          clearInterval(checkCodeBtn.timer);
          checkCodeBtn.duration = 10;
          checkCodeBtn.text = 'Reacquire'; // 设置按钮可以点击

          checkCodeBtn.disabled = false;
          checkCodeBtn.loading = false;
        }

        console.log(checkCodeBtn);
      }, 1000);
    };

    const loginPhoneStatus = ref(false);
    const loginEmailStatus = ref(true);
    const registerForms = ref(null);
    let registerFormObj = {};
    let registerFormRuleObj = {};

    if (loginPhoneStatus.value) {
      registerFormObj = {
        first_name: '',
        last_name: '',
        phone: '',
        code: '',
        password: '',
        c_password: ''
      };
      registerFormRuleObj = {
        first_name: [{
          required: true,
          message: 'Please fill out your first name.',
          trigger: 'blur'
        }],
        last_name: [{
          required: true,
          message: 'Please fill out your last name.',
          trigger: 'blur'
        }],
        phone: [{
          required: true,
          message: 'Please fill out your phone #.',
          trigger: 'blur'
        }],
        code: [{
          required: true,
          message: 'Please fill out your code',
          trigger: 'blur'
        }],
        password: [{
          required: true,
          message: 'Please enter your password',
          trigger: 'blur'
        }],
        c_password: [{
          required: true,
          message: 'Please enter your password again',
          trigger: 'blur'
        }]
      };
    }

    if (loginEmailStatus.value) {
      registerFormObj = {
        first_name: '',
        last_name: '',
        email: '',
        code: '',
        password: '',
        c_password: ''
      };
      registerFormRuleObj = {
        first_name: [{
          required: true,
          message: 'Please fill out your first name.',
          trigger: 'blur'
        }],
        last_name: [{
          required: true,
          message: 'Please fill out your last name.',
          trigger: 'blur'
        }],
        code: [{
          required: true,
          message: 'Please fill out your code.',
          trigger: 'blur'
        }],
        email: [{
          required: true,
          message: 'Please fill out your email address.',
          trigger: 'blur'
        }],
        password: [{
          required: true,
          message: 'Please enter your password',
          trigger: 'blur'
        }],
        c_password: [{
          required: true,
          message: 'Please enter your password again',
          trigger: 'blur'
        }]
      };
    }

    const registerForm = reactive(registerFormObj);
    const registerRules = reactive(registerFormRuleObj);

    const loginWithPhone = () => {
      loginPhoneStatus.value = !loginPhoneStatus.value;
      loginEmailStatus.value = !loginEmailStatus.value;
    };

    return {
      getParams,
      skipHomePage,
      showType,
      setCurrentUser,
      checkCodeBtn,
      getCheckCodeTimer,
      forgotDialogVisible,
      registerForm,
      registerRules,
      registerForms,
      loginEmailStatus,
      loginPhoneStatus,
      loginWithPhone
    };
  },

  mounted() {},

  methods: {
    test() {
      this.$msgbox({
        title: "All Set",
        message: "Let's get you logged in!",
        dangerouslyUseHTMLString: false,
        type: "success",
        center: true,
        confirmButtonText: "OK",
        "round-button": true,

        callback(action) {
          console.log(action);

          if (action === 'confirm') {
            self.$router.push({
              path: '/login',
              query: {
                phone: self.registerForm.phone
              }
            });
          }
        }

      });
    },

    turnHome() {
      this.$router.push('/');
    },

    companyInputChange(e) {
      console.log(e);
      this.completeBtnDisabledStatus = e.length <= 0;
    },

    inputChange() {
      let firstName = this.registerForm.first_name;
      let lastName = this.registerForm.last_name;
      let email = this.registerForm.email;
      let phone = this.registerForm.phone;
      let code = this.registerForm.code;
      let password = this.registerForm.password;
      let confirmPassword = this.registerForm.c_password;

      if (this.loginEmailStatus) {
        this.createAccountDisabledStatus = !(firstName && lastName && email && code && password && confirmPassword);
      }

      if (this.loginPhoneStatus) {
        this.createAccountDisabledStatus = !(firstName && lastName && phone && code && password && confirmPassword);
      }
    },

    changeType(e) {
      e.target.type = 'password';
    },

    getCheckCodeForRegister() {
      let phone = this.registerForm.phone;

      if (phone) {
        let params = {
          phone: phone
        };
        this.getCheckCodeTimer();
        WEIXIN_SEND_SMS(params).then(res => {
          console.log(res);

          if (res.code === 200) {
            this.$message.success('Success');
          }
        }).catch(err => {
          if (err.msg) {
            this.$message.error(err.msg);
          }

          if (err.message) {
            this.$message.error(err.message);
          }
        });
      }
    },

    turnToLogin() {
      this.$router.push('/login');
    },

    goHome() {
      this.$router.push('/');
    },

    remeberChange(e) {
      console.log(e);
    },

    humanVerify(response, responseKey) {
      // console.log(response);
      console.log(responseKey);
      this.$loading({
        text: 'Loading...'
      });
      let params = new FormData();
      params.append('secret', '0x55587f4f237ef6B48A96284Ee257D0AA3d828508');
      params.append('response', response);
      axios.post('/siteverify', params, {
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        baseURL: '/hcaptcha',
        timeout: 100000
      }).then(res => {
        console.log(res);

        if (res.success) {
          this.$loading().close();
          this.humanVerifyStatus = true;
          this.$message.success('Success');
        } else {
          this.$message.error('Fail');
          this.$loading().close();
        }
      });
    },

    sendEmailCode() {
      let self = this;
      let email = this.registerForm.email;

      if (email) {
        let params = {
          email: email
        };
        this.getCheckCodeTimer();
        SEND_EMAIL_CODE(params).then(res => {
          if (res.code == 200) {
            self.$message({
              type: 'success',
              message: 'Activation Code Sent'
            });
          }
        }).catch(err => {
          console.log(err);

          if (err.msg) {
            this.$message.error(err.msg);
          }

          if (err.message) {
            this.$message.error(err.message);
          }
        });
      }
    },

    selectEducationBusiness() {
      this.businessDialogStatus = true;
      this.identityBusinessCheckedStatus = false;
      this.identityValue = 0;
    },

    submitIdentityBusiness() {
      if (this.identityValue == 0) {
        return;
      }

      this.businessDialogStatus = false;
    },

    selectedIdentityBusiness(identity) {
      this.businessDialogStatus = true;

      if (identity) {
        this.identityBusinessCheckedStatus = true;
        this.identityValue = identity;
      }
    },

    selectedBusinessAll() {
      this.completeBtnDisabledStatus = true;
      this.identityValue = 0;
      this.identityBusinessCheckedStatus = true;
      this.businessDialogStatus = true;
      this.companyName = '';
    },

    selectedIdentity(value) {
      this.companyName = '';
      this.completeBtnDisabledStatus = value != 1;
      this.identityBusinessCheckedStatus = false;
      this.businessDialogStatus = false;
      this.identityValue = value;
    },

    createAccount(formName) {
      let self = this;
      this.$refs[formName].validate(valid => {
        if (valid) {
          // console.log(valid)
          self.stepValue = 2;
        } else {
          console.log('error submit!!');
          this.submitRegisterLoadingStatus = false;
          return false;
        }
      });
    },

    completeBack() {
      this.stepValue = 1;
    },

    submitRegisterForm(formName) {
      let self = this;
      this.submitRegisterLoadingStatus = true;
      this.$refs[formName].validate(valid => {
        if (valid) {
          let identityValue = self.identityValue;
          let aParams = {
            identity: identityValue
          };

          if (identityValue == 2 || identityValue == 4 || identityValue == 3 || identityValue == 5) {
            aParams.company_name = self.companyName;
          }

          let params = Object.assign(aParams, self.registerForm);

          if (self.loginPhoneStatus) {
            PHONE_REGISTER_V2(params).then(res => {
              console.log(res);

              if (res.code == 200) {
                // let userInfo = res.message
                // localStorage.setItem('uid',res.message.id)
                if (self.identityValue == 1) {
                  this.submitEducatorContactForm(res.message.id);
                }

                if (self.identityValue == 2 || self.identityValue == 3 || self.identityValue == 4 || self.identityValue == 5) {
                  this.submitCompanyContactForm(res.message.id);
                }

                self.submitRegisterLoadingStatus = false;
                this.$msgbox({
                  title: "All Set",
                  message: "Let's get you logged in!",
                  dangerouslyUseHTMLString: false,
                  type: "success",
                  center: true,
                  confirmButtonText: "OK",
                  "round-button": true,

                  callback(action) {
                    console.log(action);

                    if (action === 'confirm') {
                      self.$router.push({
                        path: '/login',
                        query: {
                          phone: self.registerForm.phone
                        }
                      });
                    }
                  }

                }); // window.location.reload()
              }
            }).catch(err => {
              console.log(err);
              self.submitRegisterLoadingStatus = false;
              this.$message.error(err.msg);
            });
          }

          if (self.loginEmailStatus) {
            EMAIL_REGISTER_V2(params).then(res => {
              console.log(res);

              if (res.code == 200) {
                // let userInfo = res.message
                // localStorage.setItem('uid',res.message.id)
                if (self.identityValue == 1) {
                  this.submitEducatorContactForm(res.message.id);
                }

                if (self.identityValue == 2 || self.identityValue == 3 || self.identityValue == 4 || self.identityValue == 5) {
                  this.submitCompanyContactForm(res.message.id);
                }

                self.submitRegisterLoadingStatus = false;
                this.$msgbox({
                  title: "All Set",
                  message: "Let's get you logged in!",
                  dangerouslyUseHTMLString: false,
                  type: "success",
                  center: true,
                  confirmButtonText: "OK",
                  "round-button": true,

                  callback(action) {
                    if (action === 'confirm') {
                      self.$router.push({
                        path: '/login',
                        query: {
                          email: self.registerForm.email
                        }
                      });
                    }
                  }

                });
              }
            }).catch(err => {
              console.log(err);
              self.submitRegisterLoadingStatus = false;
              this.$message.error(err.msg);
            });
          }
        } else {
          console.log('error submit!!');
          this.submitRegisterLoadingStatus = false;
          return false;
        }
      });
    },

    submitRegisterPhoneForm(formName) {
      let self = this;
      this.submitRegisterLoadingStatus = true;
      this.$refs[formName].validate(valid => {
        if (valid) {
          // console.log(valid)
          let params = Object.assign({
            identity: self.identityValue
          }, this.registerPhoneForm);
          PHONE_REGISTER_V2(params).then(res => {
            console.log(res);

            if (res.code == 200) {
              // let userInfo = res.message
              // localStorage.setItem('uid',res.message.id)
              if (self.identityValue == 1) {
                this.submitEducatorContactForm(res.message.id);
              }

              if (self.identityValue == 2 || self.identityValue == 3 || self.identityValue == 4 || self.identityValue == 5) {
                this.submitCompanyContactForm(res.message.id);
              }

              self.submitRegisterLoadingStatus = false;
              this.$msgbox({
                title: "All Set",
                message: "Let's get you logged in!",
                dangerouslyUseHTMLString: false,
                type: "success",
                center: true,
                confirmButtonText: "OK",
                "round-button": true,

                callback(action) {
                  console.log(action);

                  if (action === 'confirm') {
                    self.$router.push({
                      path: '/login',
                      query: {
                        type: 'login',
                        phone: self.registerPhoneForm.phone
                      }
                    });
                    self.showValue = 'login';
                  }
                }

              }); // window.location.reload()
            }
          }).catch(err => {
            console.log(err);
            self.submitRegisterLoadingStatus = false;
            this.$message.error(err.msg);
          });
        } else {
          console.log('error submit!!');
          this.submitRegisterLoadingStatus = false;
          return false;
        }
      });
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
    },

    async googleSignIn() {
      try {
        const googleUser = await this.$gAuth.signIn();

        if (!googleUser) {
          return null;
        }

        console.log("googleUser", googleUser);
        this.user = googleUser.getBasicProfile().getEmail();
        console.log("getId", this.user);
        console.log("getBasicProfile", googleUser.getBasicProfile());
        console.log("getAuthResponse", googleUser.getAuthResponse());
        console.log("getAuthResponse", this.$gAuth.instance.currentUser.get().getAuthResponse());
      } catch (error) {
        //on fail do something
        console.error(error);
        return null;
      }
    },

    async handleClickGetAuthCode() {
      try {
        const authCode = await this.$gAuth.getAuthCode();
        console.log("authCode", authCode);
      } catch (error) {
        //on fail do something
        console.error(error);
        return null;
      }
    },

    linkedinSignIn() {
      let client_id = process.env.VUE_APP_LINKEDIN_CLIENT_ID;
      let response_type = process.env.VUE_APP_LINKEDIN_RESPONSE_TYPE;
      let redirect_uri = process.env.VUE_APP_LINKEDIN_REDIRECT_URI; // let redirect_uri = 'https://dev.esl-passport.cn/login/linkedin'

      let state = randomString();
      let scope = process.env.VUE_APP_LINKEDIN_SCOPE;
      window.location.href = 'https://www.linkedin.com/oauth/v2/authorization?response_type=' + response_type + '&client_id=' + client_id + '&redirect_uri=' + redirect_uri + '&state=' + state + '&scope=' + scope;
    },

